<template>
  <v-container>
    <v-card class="transparent numberbg mx-auto" max-width="600">
      <v-card-title>
        <h2 class="mx-auto">Identify Yourself</h2>
      </v-card-title>
      <v-card-text>
        <div class="my-2">
          <p class="text-center">
            টাকা পাঠানোর সময় যে ইমেইল ব্যবহার করেছিলে এইখানেও একই ইমেইল ব্যবহার
            করে প্রবেশ করবে।
          </p>
        </div>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="cred"
            type="email"
            label="Email (Optional)"
            placeholder="Email"
            :rules="emailRules"
          />
          <v-text-field
            v-model="password"
            type="password"
            label="Password"
            placeholder="Password"
          />

          <div v-if="unknown">
            <v-text-field
              v-model="phone"
              label="Mobile No."
              placeholder="Mobile No."
              prefix="+880"
              :rules="[
                (v) => !!v || 'Phone number is required',
                (v) =>
                  (/\d{10}/.test(v) && v.length == 10) ||
                  'Phone number must be valid',
              ]"
            />
            <v-text-field
              v-model="name"
              label="Name"
              placeholder="Name"
              :rules="[(v) => !!v || 'Name is required']"
            />

            <v-text-field
              v-model="school"
              label="College Name"
              placeholder="College Name"
              :rules="[(v) => !!v || 'College name is required']"
            />
            <v-select
              v-model="gender"
              :items="['Male', 'Female']"
              label="Gender"
              :rules="[(v) => !!v || 'Gender is required']"
            />

            <v-select
              :items="[
                'Dhaka',
                'Mymensingh',
                'Chattagram',
                'Rajshahi',
                'Barishal',
                'Syhlet',
                'Rangpur',
                'Khulna',
              ]"
              label="Division"
              v-model="division"
              :rules="[(v) => !!v || 'Division is required']"
            />
          </div>
          <v-sheet
            color="success"
            class="pa-3"
            dark
            v-if="this.$store.state.error"
            >{{ this.$store.state.error }}</v-sheet
          >
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn
          v-if="!unknown"
          @click="checkPhoneNumber"
          tile
          large
          class="primary ma-auto"
          :loading="loading"
          >Submit</v-btn
        >
        <v-btn
          v-else
          @click="submitInformation"
          tile
          large
          class="primary ma-auto"
          :loading="loading"
          >Submit</v-btn
        >
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<script>
import { auth } from "@/plugins/config.js";
export default {
  data() {
    return {
      valid: true,
      loading: false,
      phone: "",
      name: "",
      cred: "",
      gender: "",
      school: "",
      division: "",

      unknown: false,
      emailRules: [
        (v) =>
          /^(([^<>()[\]\\.,;:\s@\\"]+(\.[^<>()[\]\\.,;:\s@\\"]+)*)|(\\".+\\"))@(([^<>()[\]\\.,;:\s@\\"]+\.)+[^<>()[\]\\.,;:\s@\\"]{2,})$/i.test(
            v
          ) || "Email must be valid",
      ],
    };
  },
  methods: {
    checkPhoneNumber() {
      if (this.$refs.form.validate()) {
        this.loading = !this.loading;
        fetch(auth + `?email=${this.cred}`)
          .then((res) => res.json())
          .then((data) => {
            this.loading = !this.loading;
            if (data.message == "exists") {
              this.login();
            } else {
              this.unknown = !this.unknown;
            }
          });
      }
    },
    submitInformation() {
      if (this.$refs.form.validate()) {
        this.loading = !this.loading;
        fetch(auth, {
          method: "POST",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify([
            this.phone,
            this.name,
            this.school,
            this.cred,

            this.gender,
            this.division,
          ]),
        })
          .then((res) => res.json())
          .then(() => {
            this.login();
            this.loading = !this.loading;
          });
      }
    },
    login() {
      if (/[\d]{11}/g.test(this.cred) && this.password) {
        this.$store.commit("SET_ERROR", "");
        let userdata = {
          cred: this.cred + "@asg.com",
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else if (
        /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i.test(
          this.cred
        ) &&
        this.password
      ) {
        this.$store.commit("SET_ERROR", "");

        let userdata = {
          cred: this.cred,
          password: this.password,
        };
        this.$store.dispatch("userLogin", userdata);
      } else {
        this.$store.commit("SET_ERROR", "Please provide valid credential.");
      }

      // if (userdata.phone && userdata.password) {
      //   this.$store.dispatch("userLogin", userdata);
      // }
    },
  },
};
</script>

<style lang="scss" scoped>
.numberbg {
  background: rgba(255, 255, 255, 0.4) !important;
}
</style>